.header-menu{
  transform: translateY(-100%);
  transition: all .4s ease;
  z-index: 2;
  opacity: 0;
  position: fixed;
  background: $color-default-white;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 10%;
}

.header-menu._active{
  transform: translateY(0px);
  opacity: 1;
}

.header-menu__wrapper{
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.header-menu__top-block{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 40px;
  border-bottom: 1px solid #EAE9E8;
  max-width: 1150px;
  :last-child{
    border: none;
    grid-template-columns: 80px;
  }
}

.header-menu__brand{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: $color-arapawa;
  transition: all .3s ease;
}

.header-menu__bottom-block{
  padding: 20px 20px 25px;
  max-width: 521px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

  a:hover{
    color: rgb(81, 140, 165);
  }
}

.header-menu__menu{
  display: none;
}

.header-menu__menu-right-block{
  &--bottom{
    display: none;
  }
}



@media (max-width: 960px) {


  .header-menu{
    height: 100vh;
    width: 100vw;
    overflow: auto;
    margin: 0;
  }

  .header-menu__menu{
    margin: 40px 0px 40px;
    width: 100%;
    height: 100%;
    max-width: 950px;
    max-height: 18px;
    padding: 0px 25px;
    display: flex;
    justify-content: space-between;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $color-blue-whale;

    a{
      color: $color-blue-whale;
    }

  }


  .header-menu__item-search{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $color-blue-whale;
    display: flex;
    justify-content: space-around;
    width: 100%;
    max-width: 280px;

    .header-menu__item-input{
      border: none;
      border-bottom: 2px solid #0F303F;
      background-color: transparent;
      color: inherit;
      outline: none;
      width: 150px;

      &--bottom{
        width: 200px;
      }

    }
  }

  .header-menu__menu-right-block{
    display: flex;
    max-height: 18px;
    width: 100%;
    justify-content: space-around;

    &--bottom{
      display: none;
    }
  }

  .header-menu__menu-left-block{
    display: flex;
    max-height: 18px;
    max-width: 240px;
    width: 100%;
    justify-content: space-around;
  }

  .header-menu__lang-wrapper{
    :first-child{
      margin-right: 18px;
      margin-left: 15px;
    }
  }

  .header-menu__wrapper{
    width: 100%;
    height: 100%;
    justify-content: space-between;
  }

  .header-menu__column{
    max-width: 180px;
    width: 100%;
    text-align: center;
  }

  .header-menu__top-block{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: none;

    &:last-child{
      margin-bottom: 20px;
    }
  }

  .header-menu__bottom-block{
    padding-bottom: 45px;
  }

}


@media (max-width: 600px) {
  .header-menu__menu-right-block{
    &--bottom{
    display: flex;
    max-height: 18px;
    width: 100%;
    justify-content: space-around;
    padding: 0px 25px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $color-blue-whale;

    a{
      color: $color-blue-whale;
    }
    }

    &--top{
      display: none;
    }
  }

  .header-menu__menu{
    align-items: center;
    justify-content: center;
  }
}

@media (max-width: 500px) {

  .header-menu__brand{
    font-size: 8px;
  }

  .header-menu__bottom-block{
    padding-bottom: 25px;
  }

  .header-menu__lang-wrapper{
    :first-child{
      margin-right: 9px;
      margin-left: 8px;
    }

}
}
